$breakpoints: (
  phone: 480px,
  tablet: 768px,
  big-tablet: 1024px,
  desktop: 1440px,
);

.players{
    display: flex;
    flex-direction: column;
    align-items: center;
    @media (min-width:768px){
        flex-direction: row;
    }
}

.countPlayers{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    animation: slide-left 1000ms ease-in-out;
}

@keyframes slide-left {
    from {
    margin-right: -100%;
    width: 300%; 
    }

    to {
    margin-right: 0%;
    width: 100%;
    }
}